import playBot from './playbot';

export default {
	components:{playBot},
	name: "album",
	props:{
		prodata: {
			type: Array
		}
	},
	data() {
		return {
			imgSrc: '../../static/image/',
		};
	},
	mounted:function () {
		// 因为小程序模板不能调用方法所以在这里格式化数据
		for(let i=0;i<this.prodata.length;i++){
			this.prodata[i].count = this.$pubFuc.peoNum(this.prodata[i].count)
		}
	},
	computed: {
		
	},
	methods:{
		goUrl : function (str,val){
			uni.navigateTo({
				url: str +"?music=" + JSON.stringify(val)
			});
		}
	}
}
